import React, { useState } from 'react';
import {
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Divider,
  Box,
  Typography,
  IconButton,
  Tooltip,
  Badge,
} from '@mui/material';
import {
  Dashboard as DashboardIcon,
  ViewKanban as KanbanIcon,
  CalendarToday as CalendarIcon,
  Timeline as TimelineIcon,
  List as ListIcon,
  Person as PersonIcon,
  Settings as SettingsIcon,
  Star as StarIcon,
  Search as SearchIcon,
  Folder,
  Work,
  School,
  Add as AddIcon,
} from '@mui/icons-material';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useTheme } from '../contexts/ThemeContext';
import { motion } from 'framer-motion';
import toast from 'react-hot-toast';

interface SidebarProps {
  open: boolean;
  onToggle: () => void;
}

const Sidebar: React.FC<SidebarProps> = ({ open, onToggle }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const { user } = useAuth();
  const { isDarkMode, toggleTheme } = useTheme();
  const [currentProject, setCurrentProject] = useState<string | null>(null);
  const [currentWorkspace, setCurrentWorkspace] = useState<string | null>(null);

  const menuItems = [
    {
      text: '',
      icon: <DashboardIcon />,
      path: '/',
      badge: null,
    },
    {
      text: '',
      icon: <KanbanIcon />,
      path: '/kanban',
      badge: null,
    },
    {
      text: '',
      icon: <CalendarIcon />,
      path: '/calendar',
      badge: null,
    },
    {
      text: '',
      icon: <TimelineIcon />,
      path: '/timeline',
      badge: null,
    },
    {
      text: '',
      icon: <ListIcon />,
      path: '/list',
      badge: null,
    },
  ];

  const workspaceItems = [
    {
      text: '',
      icon: <PersonIcon />,
      path: '/workspace/personal',
      badge: 0, // 
    },
    {
      text: 'A',
      icon: <Folder />,
      path: '/workspace/team-a',
      badge: 0, // 
    },
    {
      text: 'X',
      icon: <Folder />,
      path: '/workspace/project-x',
      badge: 0, // 
    },
  ];

  // 
  const calculateTaskCount = (filterType: string, filterValue: string) => {
    const allTasks = JSON.parse(localStorage.getItem('tasks') || '[]');
    return allTasks.filter((task: any) => {
      if (filterType === 'assignee') {
        return task.assignee === filterValue;
      } else if (filterType === 'status') {
        return task.status === filterValue;
      } else if (filterType === 'priority') {
        return task.priority === filterValue;
      }
      return false;
    }).length;
  };

  // 
  const updateBadgeCounts = () => {
    workspaceItems[0].badge = calculateTaskCount('assignee', '');
    workspaceItems[1].badge = calculateTaskCount('status', 'inProgress');
    workspaceItems[2].badge = calculateTaskCount('priority', 'high');
  };

  // 
  React.useEffect(() => {
    updateBadgeCounts();
    
    // 
    const savedProject = localStorage.getItem('currentProject');
    const savedWorkspace = localStorage.getItem('currentWorkspace');
    
    if (savedProject) {
      setCurrentProject(savedProject);
    }
    if (savedWorkspace) {
      setCurrentWorkspace(savedWorkspace);
    }
  }, []);

  // 
  React.useEffect(() => {
    const savedProject = localStorage.getItem('currentProject');
    const savedWorkspace = localStorage.getItem('currentWorkspace');
    
    if (savedProject !== currentProject) {
      setCurrentProject(savedProject);
    }
    if (savedWorkspace !== currentWorkspace) {
      setCurrentWorkspace(savedWorkspace);
    }
  }, []); // 

  const handleNavigation = (path: string) => {
    navigate(path);
  };

  const handleProjectClick = (projectName: string) => {
    // 
    console.log(` "${projectName}" `);
    
    // 
    setCurrentProject(projectName);
    setCurrentWorkspace(null);
    
    // 
    const allTasks = JSON.parse(localStorage.getItem('tasks') || '[]');
    let filteredTasks = [];
    
    switch (projectName) {
      case '':
        filteredTasks = allTasks.filter((task: any) => !task.assignee || task.assignee === '');
        break;
      case '':
        filteredTasks = allTasks.filter((task: any) => task.assignee === '' || task.priority === 'high');
        break;
      case '':
        filteredTasks = allTasks.filter((task: any) => task.assignee === '' || task.priority === 'medium');
        break;
      default:
        filteredTasks = allTasks;
    }
    
    // 
    localStorage.setItem('filteredTasks', JSON.stringify(filteredTasks));
    localStorage.setItem('currentProject', projectName);
    localStorage.removeItem('currentWorkspace'); // 
    
    // 
    setCurrentProject(projectName);
    // 
    setTimeout(() => {
      window.location.reload();
    }, 100);
    
    // 
    toast.success(`${projectName}`);
  };

  const handleWorkspaceClick = (workspaceName: string) => {
    // 
    console.log(` "${workspaceName}" `);
    
    // 
    setCurrentWorkspace(workspaceName);
    setCurrentProject(null);
    
    // 
    const allTasks = JSON.parse(localStorage.getItem('tasks') || '[]');
    let filteredTasks = [];
    
    switch (workspaceName) {
      case '':
        filteredTasks = allTasks.filter((task: any) => !task.assignee || task.assignee === '');
        break;
      case 'A':
        filteredTasks = allTasks.filter((task: any) => task.assignee === 'A' || task.status === 'inProgress');
        break;
      case 'X':
        filteredTasks = allTasks.filter((task: any) => task.assignee === 'X' || task.priority === 'high');
        break;
      default:
        filteredTasks = allTasks;
    }
    
    // 
    localStorage.setItem('filteredTasks', JSON.stringify(filteredTasks));
    localStorage.setItem('currentWorkspace', workspaceName);
    localStorage.removeItem('currentProject'); // 
    
    // 
    setCurrentWorkspace(workspaceName);
    // 
    setTimeout(() => {
      window.location.reload();
    }, 100);
    
    // 
    toast.success(`${workspaceName}`);
  };

  if (!user) {
    return null;
  }

  return (
    <Drawer
      variant="permanent"
      open={open}
      sx={{
        width: open ? 240 : 64, // 
        flexShrink: 0,
        '& .MuiDrawer-paper': {
          width: open ? 240 : 64, // 
          overflowX: 'hidden',
          transition: 'width 0.3s ease-in-out',
        },
      }}
    >
      <Box sx={{ p: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        {open && (
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.3 }}
          >
            <Typography variant="h6" sx={{ fontWeight: 600, color: 'primary.main' }}>
              TaskFlow
            </Typography>
          </motion.div>
        )}
        <Tooltip title={open ? '' : ''}>
          <IconButton onClick={onToggle} size="small">
            <SearchIcon />
          </IconButton>
        </Tooltip>
      </Box>

      <Divider />

      <Box sx={{ p: 2 }}>
        <Tooltip title="" placement="right">
          <IconButton
            color="primary"
            sx={{
              width: '100%',
              height: 48,
              borderRadius: 2,
              backgroundColor: 'primary.main',
              color: 'white',
              '&:hover': {
                backgroundColor: 'primary.dark',
              },
            }}
            onClick={() => navigate('/task/new')}
          >
            <AddIcon />
            {open && (
              <motion.div
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.3 }}
                style={{ marginLeft: 8 }}
              >
                <Typography variant="body2" sx={{ fontWeight: 500 }}>
                  
                </Typography>
              </motion.div>
            )}
          </IconButton>
        </Tooltip>
      </Box>

      <Divider />

      <List sx={{ px: 1 }}>
        {menuItems.map((item) => (
          <ListItem key={item.text} disablePadding sx={{ mb: 0.5 }}>
            <ListItemButton
              onClick={() => handleNavigation(item.path)}
              selected={location.pathname === item.path}
              sx={{
                borderRadius: 2,
                minHeight: 48,
                '&.Mui-selected': {
                  backgroundColor: 'primary.main',
                  color: 'white',
                  '&:hover': {
                    backgroundColor: 'primary.dark',
                  },
                },
                '&:hover': {
                  backgroundColor: 'action.hover',
                },
              }}
            >
              <ListItemIcon
                sx={{
                  minWidth: 40,
                  color: location.pathname === item.path ? 'white' : 'inherit',
                }}
              >
                {item.icon}
              </ListItemIcon>
              {open && (
                <motion.div
                  initial={{ opacity: 0, x: -10 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.3 }}
                  style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}
                >
                  <ListItemText
                    primary={item.text}
                    primaryTypographyProps={{
                      fontSize: '0.875rem',
                      fontWeight: location.pathname === item.path ? 600 : 500,
                    }}
                  />
                  {item.badge && (
                    <Badge
                      badgeContent={item.badge}
                      color="error"
                      sx={{
                        '& .MuiBadge-badge': {
                          backgroundColor: location.pathname === item.path ? 'white' : 'primary.main',
                          color: location.pathname === item.path ? 'primary.main' : 'white',
                        },
                      }}
                    />
                  )}
                </motion.div>
              )}
            </ListItemButton>
          </ListItem>
        ))}
      </List>

      <Divider />

      {open && (
        <motion.div
          initial={{ opacity: 0, height: 0 }}
          animate={{ opacity: 1, height: 'auto' }}
          transition={{ duration: 0.3 }}
        >
          <Box sx={{ p: 2 }}>
            <Typography variant="overline" sx={{ color: 'text.secondary', fontWeight: 600 }}>
              
            </Typography>
          </Box>
        </motion.div>
      )}

      <List sx={{ px: 1 }}>
        {workspaceItems.map((item) => (
          <ListItem key={item.text} disablePadding sx={{ mb: 0.5 }}>
            <ListItemButton
              onClick={() => handleWorkspaceClick(item.text)}
              selected={currentWorkspace === item.text}
              sx={{
                borderRadius: 2,
                minHeight: 48,
                '&.Mui-selected': {
                  backgroundColor: 'primary.main',
                  color: 'white',
                  '&:hover': {
                    backgroundColor: 'primary.dark',
                  },
                },
                '&:hover': {
                  backgroundColor: 'action.hover',
                },
              }}
            >
              <ListItemIcon
                sx={{
                  minWidth: 40,
                  color: currentWorkspace === item.text ? 'white' : 'inherit',
                }}
              >
                {item.icon}
              </ListItemIcon>
              {open && (
                <motion.div
                  initial={{ opacity: 0, x: -10 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.3 }}
                  style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}
                >
                  <ListItemText
                    primary={item.text}
                    primaryTypographyProps={{
                      fontSize: '0.875rem',
                      fontWeight: currentWorkspace === item.text ? 600 : 500,
                    }}
                  />
                  {item.badge && (
                    <Badge
                      badgeContent={item.badge}
                      color="error"
                      sx={{
                        '& .MuiBadge-badge': {
                          backgroundColor: currentWorkspace === item.text ? 'white' : 'primary.main',
                          color: currentWorkspace === item.text ? 'primary.main' : 'white',
                        },
                      }}
                    />
                  )}
                </motion.div>
              )}
            </ListItemButton>
          </ListItem>
        ))}
      </List>

      {/*  */}
      <Box sx={{ mb: 2 }}>
        <Typography
          variant="overline"
          sx={{
            px: 2,
            py: 1,
            color: 'text.secondary',
            fontWeight: 600,
            fontSize: '0.75rem',
            display: open ? 'block' : 'none',
          }}
        >
          
        </Typography>
        <List dense>
          <ListItem disablePadding>
            <ListItemButton
              onClick={() => handleProjectClick('')}
              selected={currentProject === ''}
              sx={{
                minHeight: 48,
                px: 2,
                '&.Mui-selected': {
                  backgroundColor: 'primary.main',
                  color: 'primary.contrastText',
                  '&:hover': {
                    backgroundColor: 'primary.dark',
                  },
                },
              }}
            >
              <ListItemIcon sx={{ minWidth: 40 }}>
                <Folder sx={{ fontSize: 20 }} />
              </ListItemIcon>
              {open && (
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', flex: 1 }}>
                  <ListItemText
                    primary=""
                    primaryTypographyProps={{
                      fontSize: '0.875rem',
                      fontWeight: 500,
                    }}
                  />
                  <Badge
                    badgeContent={calculateTaskCount('assignee', '')}
                    color="primary"
                    sx={{
                      '& .MuiBadge-badge': {
                        fontSize: '0.625rem',
                        height: 18,
                        minWidth: 18,
                      },
                    }}
                  />
                </Box>
              )}
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton
              onClick={() => handleProjectClick('')}
              selected={currentProject === ''}
              sx={{
                minHeight: 48,
                px: 2,
                '&.Mui-selected': {
                  backgroundColor: 'primary.main',
                  color: 'primary.contrastText',
                  '&:hover': {
                    backgroundColor: 'primary.dark',
                  },
                },
              }}
            >
              <ListItemIcon sx={{ minWidth: 40 }}>
                <Work sx={{ fontSize: 20 }} />
              </ListItemIcon>
              {open && (
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', flex: 1 }}>
                  <ListItemText
                    primary=""
                    primaryTypographyProps={{
                      fontSize: '0.875rem',
                      fontWeight: 500,
                    }}
                  />
                  <Badge
                    badgeContent={calculateTaskCount('priority', 'high')}
                    color="error"
                    sx={{
                      '& .MuiBadge-badge': {
                        fontSize: '0.625rem',
                        height: 18,
                        minWidth: 18,
                      },
                    }}
                  />
                </Box>
              )}
            </ListItemButton>
          </ListItem>
          <ListItem disablePadding>
            <ListItemButton
              onClick={() => handleProjectClick('')}
              selected={currentProject === ''}
              sx={{
                minHeight: 48,
                px: 2,
                '&.Mui-selected': {
                  backgroundColor: 'primary.main',
                  color: 'primary.contrastText',
                  '&:hover': {
                    backgroundColor: 'primary.dark',
                  },
                },
              }}
            >
              <ListItemIcon sx={{ minWidth: 40 }}>
                <School sx={{ fontSize: 20 }} />
              </ListItemIcon>
              {open && (
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', flex: 1 }}>
                  <ListItemText
                    primary=""
                    primaryTypographyProps={{
                      fontSize: '0.875rem',
                      fontWeight: 500,
                    }}
                  />
                  <Badge
                    badgeContent={calculateTaskCount('priority', 'medium')}
                    color="warning"
                    sx={{
                      '& .MuiBadge-badge': {
                        fontSize: '0.625rem',
                        height: 18,
                        minWidth: 18,
                      },
                    }}
                  />
                </Box>
              )}
            </ListItemButton>
          </ListItem>
        </List>
      </Box>

      <Box sx={{ flexGrow: 1 }} />

      <Divider />

      <List sx={{ px: 1 }}>
        <ListItem disablePadding sx={{ mb: 0.5 }}>
          <ListItemButton
            onClick={() => navigate('/profile')}
            sx={{
              borderRadius: 2,
              minHeight: 48,
              '&:hover': {
                backgroundColor: 'action.hover',
              },
            }}
          >
            <ListItemIcon sx={{ minWidth: 40 }}>
              <PersonIcon />
            </ListItemIcon>
            {open && (
              <motion.div
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.3 }}
              >
                <ListItemText
                  primary=""
                  primaryTypographyProps={{
                    fontSize: '0.875rem',
                    fontWeight: 500,
                  }}
                />
              </motion.div>
            )}
          </ListItemButton>
        </ListItem>

        <ListItem disablePadding sx={{ mb: 0.5 }}>
          <ListItemButton
            onClick={() => navigate('/settings')}
            sx={{
              borderRadius: 2,
              minHeight: 48,
              '&:hover': {
                backgroundColor: 'action.hover',
              },
            }}
          >
            <ListItemIcon sx={{ minWidth: 40 }}>
              <SettingsIcon />
            </ListItemIcon>
            {open && (
              <motion.div
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.3 }}
              >
                <ListItemText
                  primary=""
                  primaryTypographyProps={{
                    fontSize: '0.875rem',
                    fontWeight: 500,
                  }}
                />
              </motion.div>
            )}
          </ListItemButton>
        </ListItem>
      </List>
    </Drawer>
  );
};

export default Sidebar; 